import { ChatArea, Form, MentionsTextarea, SendButton, Toolbox } from '@components/ChatBox/styles';
import React, { useCallback, useEffect, useRef, VFC } from 'react';
import autosize from 'autosize';

interface Props {
  chat?: string;
  onSubmitForm: (e: any) => void;
  onChangeChat: (e: any) => void;
  placeholder?: string;
}

const ChatBox: VFC<Props> = ({ chat, onSubmitForm, onChangeChat, placeholder }) => {
  /*
  const onSubmitForm = useCallback(() => {
    // - DM 보내기를 여기서 하면 채널에서 onSubmitForm을 사용할 수 없음
    // - 채널 메세지 보내기를 여기서 하면 DM에서 onSubmitForm을 사용할 수 없음
    // 여러 곳에서 재사용 되는데 상세 내용이 다를 경우, onSubmitForm을 props로 올려줌
    // (자세한 작업이 들어가면 다른 곳에서 재사용할 수 없어짐)
    // (두루두로 쓰고싶으면 props로 올려서 나를 사용하는 부모에서 구체적인 작업을 하도록 만들어야함!! -> component입장에서의 props 사용 목적이 될 수 있음)
  }, []);
  */

  // autosize 라이브러리 적용하기
  /*
  npm 에서 활발하게 업데이트 되고 있는 라이브러리를 사용하면 좋지만
  최근 업데이트가 3년 전임에도 사람들이 많이 사용하는 라이브러리의 경우가 있음
  너무 완벽해서 더 이상 바꿀 필요가 없는 경우
  3년 전에 개발이 끝났지만 굳이 수정이 필요하지 않은 경우가 간혹 있음 이런 경우는 써도 됨
  그러나 대부분은 Last publish가 최신인 것이 좋음
  */
  const textareaRef = useRef(null);
  useEffect(() => {
    if (textareaRef.current) {
      autosize(textareaRef.current);
    }
  }, []);

  const onKeydownChat = useCallback(
    (e) => {
      if (e.key === 'Enter') {
        if (!e.shiftKey) {
          e.preventDefault();
          onSubmitForm(e);
        }
      }
    },
    [onSubmitForm], // props로 받은 것들은 어지간하면 deps에 넣어줘야함
  );

  return (
    <ChatArea>
      <Form onSubmit={onSubmitForm}>
        <MentionsTextarea
          id="editor-chat"
          value={chat}
          onChange={onChangeChat}
          onKeyDown={onKeydownChat}
          placeholder={placeholder}
          ref={textareaRef} /*태그에 직접 접근하고 싶을 때 ref를 사용함*/
          /*
          타입스크립트에서 ref를 사용할 경우 error가 날 수 있음

          const textareaRef = useRef(null);
          useEffect(() => {
            if (textareaRef.current) {
              autosize(textareaRef.current);
            }
          }, []);

          null과 .current를 적용하면 error 해결할 수 있음
          */
        />
        {/*value와 onChange는 항상 같이 있어야 합니다.*/}
        <Toolbox>
          <SendButton
            className={
              'c-button-unstyled c-icon_button c-icon_button--light c-icon_button--size_medium c-texty_input__button c-texty_input__button--send' +
              (chat?.trim() ? '' : ' c-texty_input__button--disabled')
            }
            data-qa="texty_send_button"
            aria-label="Send message"
            data-sk="tooltip_parent"
            type="submit"
            disabled={!chat?.trim()}
          >
            <i className="c-icon c-icon--paperplane-filled" aria-hidden="true" />
          </SendButton>
        </Toolbox>
      </Form>
    </ChatArea>
  );
};

export default ChatBox;
